<template>
	<view class="page_goods" id="goods_list">
		<!-- 搜索栏 -->
		<uni-search-bar placeholder="搜索产品" @confirm="search" @cancel="cancel" cancelText="取消" @input="input($event, 'title')">
			<uni-icons slot="searchIcon" color="#999999" size="18" type="home" />
		</uni-search-bar>
		<!-- /搜索栏 -->
		
		<!-- 商品列表 -->
		<list_goods :list="list" class="mb"></list_goods>
		<!-- /商品列表 -->
		
		<!-- 分页器 -->
		<uni-pagination class="pager" show-icon="true" :total="count" :pageSize="query.size" :current="query.page"
		 @change="page_change"></uni-pagination>
		 <!-- /分页器 -->
	</view>
</template>

<script>
	import list_goods from "../../components/diy/list_goods.vue";

	import mixin from "../../mixins/page.js"

	export default {
		mixins: [mixin],
		components: {
			list_goods
		},
		data() {
			return {
				url_get_list: "~/api/goods/get_list?",
				list: [
				],
				query: {
					title: "",
					page: 1,
					size: 10
				},
				count: 50
			}
		},
		methods: {
			input(e, key){
				this.query[key] = e.value;
			},
			page_change(e) {
				console.log(e);
			},
			search() {
				this.query.page = 1;
				this.get_list();
			},
			cancel() {
				console.log("eer");
				this.query.title = "";
				this.search();
			}
		}
	}
</script>

<style scoped>
	.pager {
		margin-top: 1rem;
	}
</style>
